body {
  margin: 0;
  padding: 0;
  font-family: Sans-Serif;
}

h1 {
	margin: 0;
	color: #f6f7ee;
}

.header {
	margin-top: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	min-width: 360px;
}

.count {
	padding: 2px;
	font-size: 10pt;
	border-width: 1px;
	border-style: solid;
	background-color: #23221c;
	color: #686964;
}

.count span {
	text-align: center;
	font-size: 16pt;
	display: block;
}

.content {
	  max-width: 150vh;
    margin-left: 10%;
}

.newgame {
	white-space: nowrap;
	margin-right: 10px;
	padding: 5px;
	background: -webkit-gradient(linear, left top, left bottom, from(#e0e0e0), to(#b0b0b0));
	background: -webkit-linear-gradient(top, #e0e0e0, #b0b0b0);
	background: -o-linear-gradient(top, #e0e0e0, #b0b0b0);
	background: linear-gradient(to bottom, #e0e0e0, #b0b0b0);
	cursor: pointer;
	margin: 0;
}


